import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import DebitMemoUpdateForm from "./updateForm";
import DebitMemoDocUpdateForm from "./updateDocForm";
import History from "./history";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/debitMemo/actions";
import { Box } from "../../../App/app.style";
import { Tabs, Tooltip } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/financials/debit-memos";


class UpdateDebitMemo extends Component {
  state = {
    visible: false
  }
  // Get All data diawal
  componentDidMount() {
    const { editDebitMemo } = this.props;
    const { id } = this.props.match.params;
    editDebitMemo(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.debitMemo.error) {
      notification("error", this.props.debitMemo.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.debitMemo.success) {
      notification("success", this.props.debitMemo.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateDebitMemo } = this.props;
    const { id } = this.props.match.params;

    updateDebitMemo(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelDebitMemo } = this.props;
    const { id } = this.props.match.params;

    cancelDebitMemo(id, value);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    // console.log(this.props);
    const { debit_memo, loading } = this.props.debitMemo
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="debitMemos.update.title" />}
            style={{ margin:0 }}
          >
             <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={debit_memo} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={debit_memo ? debit_memo.description : null}
          />
          
          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="debitMemos.tab.detail" />} 
              key="1" 
            >
              {this.props.debitMemo.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <DebitMemoUpdateForm
                  submit={this.handleSubmit}
                  debitMemo={this.props.debitMemo}
                  permissions={permissions}
                  processDebitMemo={this.props.processDebitMemo}
                  handleSubmitCancel={this.handleSubmitCancel}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="debitMemos.tab.attachment" />} 
              key="2"
            >  
             {this.props.debitMemo.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <DebitMemoDocUpdateForm
                  submit={this.handleSubmit}
                  debitMemo={this.props.debitMemo}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    debitMemo: state.debitMemo, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateDebitMemo);
