import React, { Component } from "react";
import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Col, InputNumber, DatePicker, Popconfirm, Steps, Row, Empty, Radio } from "antd";
import { customDot } from "../../../App/app.style";
import ModalCancel from "./modalCancel";
import { getRate } from "../../../../helpers/helper";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class DebitMemoUpdateForm extends Component {
  state = {
    currency_is_base: 2,
    twin_error: null,
    visibleModalCancel: false
  }

  componentDidUpdate = () => {
    if (this.state.currency_is_base === 2 && this.props.debitMemo.debit_memo !== null) {
      this.setState({
        currency_is_base: this.props.debitMemo.debit_memo.currency.is_base
      })
    }
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  // Cross GL Account dan Cash bank
  handleChangeTwinError = () => {
    this.setState({
      twin_error: null,
    })
  }

  setFieldRate = (currency_rates, date) => {
    const { setFieldsValue } = this.props.form

    let rate = getRate(currency_rates, date)
    // console.log(rate)
    setFieldsValue({
      ["currency_rate"]: rate.currency_rate,
      ["fiscal_rate"]: rate.fiscal_rate,
    })
  }

  handleChangeTransDate = (e) => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    if (getFieldValue('transaction_date') > getFieldValue('due_date')) {
      setFieldsValue({
        ["due_date"]: null
      })
    }
    if (e instanceof moment) {
      if (getFieldValue("id_currency")) {
        this.props.debitMemo.currencies.map((x) => {
          if (x.id_currency === getFieldValue("id_currency")) {
            this.setFieldRate(x.currency_rate, e)
            //console.log("")
          }
        })
      }
    }
  }

  handleChangeGLAccount = (e) => {
    const { getFieldValue, setFieldsValue } = this.props.form;

    if (e === getFieldValue('id_cross_gl_account')) {
      setFieldsValue({
        ["id_cross_gl_account"]: null
      })
    }
  }

  handleChangeSupplier = (e) => {
    this.props.debitMemo.suppliers.map((data) => {
      if (data.id_supplier === e) {
        const { setFieldsValue, getFieldValue } = this.props.form
        setFieldsValue({
          id_currency: data.id_currency,
          id_cash_bank: null
        })
        this.props.debitMemo.currencies.map((x) => {
          if (x.id_currency === data.id_currency) {
            this.setState({ currency_is_base: x.is_base })
            this.setFieldRate(x.currency_rate, getFieldValue("transaction_date"))
          }
        })
      }
    })
  }

  handleChangeLocation = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_cash_bank: null,
      id_supplier: null
    })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      // const { getFieldValue } = this.props.form;

      // if(getFieldValue('id_cash_bank') === undefined && getFieldValue('id_cross_gl_account') === undefined){
      //   this.setState({
      //     twin_error: "One of Cross GL Account or Cash Bank is Required"
      //   })
      // }
      // else 
      if (!err) {
        values.transaction_date = values.transaction_date.format("YYYY-MM-DD HH:mm:ss");
        values.due_date = moment(values.transaction_date, 'YYYY-MM-DD').format("YYYY-MM-DD");
        this.props.submit(values);
      }
    });
  };


  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { debitMemo, processDebitMemo, cancelDebitMemo } = this.props;
    const { loading, base_currency } = debitMemo;

    const data = debitMemo.debit_memo ? debitMemo.debit_memo : [];

    let permissions = this.props.permissions;

    // const error = debitMemo.errorField ? debitMemo.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    }

    const radioGroupStyle2 = {
      display: "flex",
      width: "100%"
    }

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="debitMemos.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small"
              progressDot={customDot}
              current={
                data.status === 4
                  ? 0
                  : data.status
              }
            >
              <Step title={getLanguage({id: "new"})} />
              <Step title={getLanguage({id: "open"})} />
              <Step title={getLanguage({id: "closed"})} />
              <Step title={getLanguage({id: "cancelled"})} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div style={{ padding: "0 15px 0 0" }} className="isoColumn-2 isoFormGroupComp">

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator('transaction_type', {
                initialValue: data.transaction_type,
              })(
                <Radio.Group
                  buttonStyle="solid"
                  style={ data.transaction_type === 7 
                    ? radioGroupStyle2
                    : radioGroupStyle
                  }
                  disabled={data.status === 4 ? false : true}
                >
                  <Radio.Button style={radioButtonStyle} value={6}><IntlMessages id="supplierDeposit" /></Radio.Button>
                  {
                    data.transaction_type === 7
                      ? (<Radio.Button style={radioButtonStyle} value={7}><IntlMessages id="debitMemo" /></Radio.Button>)
                      : null
                  }
                </Radio.Group>

              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="debitMemos.no" />}
            >
              {getFieldDecorator("debit_memo_no", {
                initialValue: data.debit_memo_no,

              })(<Input
                disabled={true}
              />)}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="purchaseOrders.transactionDate" />
                  }
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment(data.transaction_date, 'YYYY-MM-DD HH:mm:ss'),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      onChange={this.handleChangeTransDate}
                      onOpenChange={this.handleChangeTransDate}
                      disabled={
                        permissions.includes("Can Back Date") && data.status === 4
                          ? false
                          : true
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.pageHeader" />}
                >
                  {getFieldDecorator("id_location", {
                    initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.debitMemo.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeLocation}
                      disabled={data.status === 4 ? false : true}
                      style={{ width: "100%" }}
                    >
                      {debitMemo.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              {/* <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="purchaseOrders.dueDate" />}
                >
                  {getFieldDecorator("due_date", {
                    initialValue: moment(data.due_date, 'YYYY-MM-DD'),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY"
                      disabled={data.status === 4 ? false : true}
                      allowClear={false}
                      disabledDate={e => e.valueOf() < getFieldValue('transaction_date').subtract(1, 'seconds') }
                    />
                  )}
                </Form.Item>
              </Col> */}
            </InputGroup>

            <InputGroup>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="suppliers.pageHeader" />}
                >
                  {getFieldDecorator("id_supplier", {
                    initialValue: data.id_supplier,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.debitMemo.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeSupplier}
                      disabled={data.status === 4 ? false : true}
                      notFoundContent={
                        getFieldValue('id_location')
                          ?
                          (
                            <Empty description={<IntlMessages id="noData" />} />
                          )
                          : (
                            <Empty
                              description={
                                <IntlMessages id="noLocation" />
                              }
                            />
                          )
                      }
                      style={{ width: "100%" }}

                    // disabled={used ? true : false}
                    >
                      {debitMemo.suppliers.map((data, key) => (
                        getFieldValue('id_location') === data.id_location
                          ?
                          (
                            <Option key={key} value={data.id_supplier}>
                              {data.supplier_code + " (" + data.supplier_name + ")"}
                            </Option>
                          )
                          : null
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="currencies.pageHeader" />}
                >
                  {getFieldDecorator("id_currency", {
                    initialValue: data.id_currency,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.debitMemo.loading}
                      showSearch
                      optionFilterProp="children"
                      disabled={true}
                      style={{ width: "100%" }}
                    >
                      {debitMemo.currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {data.currency_code + " (" + data.currency_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <InputGroup compact>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="rate" />}
                    >
                      {getFieldDecorator("currency_rate", {
                        initialValue: data.currency_rate,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <InputNumber
                          formatter={value =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          }
                          parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          min={0.01}
                          precision={2}
                          disabled={
                            data.status === 4
                              ? this.state.currency_is_base === 1
                                ? true
                                : false
                              : true
                          }
                          style={{ width: "100%" }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="fiscalRate" />}
                    >
                      {getFieldDecorator("fiscal_rate", {
                        initialValue: data.fiscal_rate,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <InputNumber
                          formatter={value =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          }
                          parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          min={0.01}
                          precision={2}
                          disabled={
                            data.status === 4
                              ? this.state.currency_is_base === 1
                                ? true
                                : false
                              : true
                          }
                          style={{ width: "100%" }}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="amount" />}
            >
              {getFieldDecorator("currency_amount", {
                initialValue: data.currency_amount,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  min={0.01}
                  precision={2}
                  disabled={data.status === 4 ? false : true}
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>

          </div>
          <div style={{ padding: "0 0 0 15px" }} className="isoColumn-2 isoFormGroupComp">

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="GLAccount" />}
                >
                  {getFieldDecorator("id_gl_account", {
                    initialValue: data.id_gl_account,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.debitMemo.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeGLAccount.bind(this)}
                      disabled={data.status === 4 ? false : true}
                      style={{ width: "100%" }}
                    // onOpenChange={this.handleChangeGLAccount}
                    // disabled={used ? true : false}
                    >
                      {debitMemo.accounts.map((data, key) => (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="cashFlowTypes.pageHeader" />}
                >
                  {getFieldDecorator("id_cash_flow_type", {
                    initialValue: data.id_cash_flow_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.debitMemo.loading}
                      showSearch
                      optionFilterProp="children"
                      disabled={data.status === 4 ? false : true}
                      style={{ width: "100%" }}
                    >
                      {debitMemo.cash_flow_types.map((data, key) => (
                        <Option key={key} value={data.id_cash_flow_type}>
                          {data.cash_flow_type_code + " (" + data.description + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="cashBanks.pageHeader" />}
            >
              {getFieldDecorator("id_cash_bank", {
                initialValue: data.id_cash_bank,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.debitMemo.loading}
                  showSearch
                  //allowClear={true}
                  optionFilterProp="children"
                  //onChange={this.handleChangeTwinError}
                  disabled={data.status === 4 ? false : true}
                  style={{ width: "100%" }}
                >
                  {getFieldValue('id_location') && getFieldValue('id_supplier') ?
                    debitMemo.cash_banks.map((data, key) => (
                      getFieldValue('id_location') === data.id_location &&
                        (
                          getFieldValue('id_currency') === data.id_currency ||
                          base_currency.id_currency === data.id_currency
                        )
                        ?
                        (
                          <Option key={key} value={data.id_cash_bank}>
                            {data.bank_account_number === null
                              ? data.cash_bank_code
                              : data.cash_bank_code + " (" + data.bank_account_number + ")"
                            }
                          </Option>
                        )
                        : null
                    ))
                    : null
                  }
                </Select>
              )}
            </Form.Item>

            {/* <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="crossGLAccount" />}
                  validateStatus={
                    this.state.twin_error 
                      ? "error"
                      : null
                  }
                  help={
                    this.state.twin_error
                  }
                >
                  {getFieldDecorator("id_cross_gl_account", {
                    initialValue: data.id_cross_gl_account,
                  })(
                    <Select
                      loading={this.props.debitMemo.loading}
                      showSearch
                      optionFilterProp="children"
                      allowClear={true}
                      onChange={this.handleChangeTwinError}
                      disabled={data.status === 4 ? false : true} 
                      style={{ width:"100%" }}                  
                    >
                      {
                        getFieldValue('id_gl_account')!==undefined
                        ?
                          debitMemo.accounts.map((data, key) => (
                            getFieldValue('id_gl_account') !== data.id_account
                            ? 
                              (
                                <Option key={key} value={data.id_account}>
                                  { data.account_code + " (" + data.account_name +")"}
                                </Option>
                              )
                            : 
                              null
                          ))
                        : null
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="cashBanks.pageHeader" />}
                  validateStatus={
                    this.state.twin_error 
                      ? "error"
                      : null
                  }
                  help={
                    this.state.twin_error
                  }
                >
                  {getFieldDecorator("id_cash_bank", {
                    initialValue: data.id_cash_bank,
                    // rules: [
                    //   {
                    //     required: true,
                    //     message: <IntlMessages id="required" />
                    //   }
                    // ]
                  })(
                    <Select
                      loading={this.props.debitMemo.loading}
                      showSearch
                      allowClear={true}
                      optionFilterProp="children"
                      onChange={this.handleChangeTwinError}
                      disabled={data.status === 4 ? false : true}    
                      style={{ width:"100%" }}               
                    >
                      { getFieldValue('id_location') ?
                        debitMemo.cash_banks.map((data, key) => (
                          getFieldValue('id_location') === data.id_location 
                          ?
                            (
                              <Option key={key} value={data.id_cash_bank}>
                                { data.bank_account_number === null
                                  ? data.cash_bank_code
                                  : data.cash_bank_code + " (" + data.bank_account_number +")"
                                }
                              </Option>
                            )
                          : null
                          ))
                        : null
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup> */}

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="issuerDestination" />}
                >
                  {getFieldDecorator("bank_issuer", {
                    initialValue: data.bank_issuer,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(<Input
                    disabled={data.status === 4 ? false : true}
                  />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="referenceNo" />}
                >
                  {getFieldDecorator("reference_no", {
                    initialValue: data.reference_no,

                  })(<Input
                    disabled={data.status === 4 ? false : true}
                  />)}
                </Form.Item>
              </Col>
            </InputGroup>


            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                initialValue: data.note,
              })(<TextArea
                autosize={{ minRows: 3 }}
                disabled={data.status === 4 ? false : true}
              />)}
            </Form.Item>

          </div>
        </div>



        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginRight: "5px" }}
            loading={loading}
            disabled={
              data.status === 4
                ? false
                : true
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={() =>
              processDebitMemo(data.id_debit_memo)
            }
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 4 &&
                  permissions.includes("Confirm Debit Memo")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 1 &&
                data.transaction_type === 6 &&
                permissions.includes("Cancel Debit Memo")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

        </Form.Item>
      </Form>
    );
  }
}

const WrappedDebitMemoUpdateForm = Form.create({ name: "update_debit_memo" })(DebitMemoUpdateForm);

export default WrappedDebitMemoUpdateForm;
