import React, { Component } from "react";
import { Row, Col } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {

  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;
    const leftSide = [
      {
        label: <IntlMessages id="transactionType" />,
        description: data.transaction_type === 6 ? <IntlMessages id="supplierDeposit" /> : <IntlMessages id="debitMemo" />
      },
      {
        label: <IntlMessages id="dueDate" />,
        description: moment(data.due_date, "YYYY-MM-DD").format("DD MMMM YYYY")
      },
      {
        label: <IntlMessages id="locations.pageHeader" />,
        description: data.location_name
      },
      {
        label: <IntlMessages id="suppliers.pageHeader" />,
        description: data.supplier_name
      },
      {
        label: <IntlMessages id="currencies.pageHeader" />,
        description: data.currency ? data.currency.currency_code : null 
      },
      {
        label: <IntlMessages id="rate" />,
        description: data.currency_rate 
            ? <NumberFormat value={data.currency_rate} />
            : null

      },
      {
        label: <IntlMessages id="fiscalRate" />,
        description: data.fiscal_rate 
            ? <NumberFormat value={data.fiscal_rate} />
            : null
      },
      {
        label: <IntlMessages id="amount" />,
        description: data.currency_amount 
            ? <NumberFormat value={data.currency_amount} />
            : null
      },
      
    ];

    const rightSide = [
      {
        label: <IntlMessages id="GLAccount" />,
        description: data.gl_account 
          ? data.gl_account.account_code + " (" + data.gl_account.account_name + ")"
          : null
      },
      {
        label: <IntlMessages id="cashFlowTypes.pageHeader" />,
        description: data.cash_flow_type 
          ? data.cash_flow_type.cash_flow_type_code + " (" + data.cash_flow_type.description + ")"
          : null
      },
      {
        label: <IntlMessages id="cashBanks.pageHeader" />,
        description: data.cash_bank 
          ? 
            data.cash_bank.bank_account_number
              ? data.cash_bank.cash_bank_code + " (" + data.cash_bank.bank_account_number + ")"
              : data.cash_bank.cash_bank_code
          : null
      },
      {
        label: <IntlMessages id="issuerDestination" />,
        description: data.bank_issuer
      },
      {
        label: <IntlMessages id="referenceNo" />,
        description: data.reference_no
      },
      {
        label: <IntlMessages id="note" />,
        description: data.note
      },
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.debitMemo" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.debit_memo_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() => document.title = getFileName("purchase_variant", data.debit_memo_no, true)}
          onAfterPrint={() => document.title = siteConfig.documentTitle}
        />
      </>
    );
  }
}

export default Print;
